'use client';

import { useState } from 'react';
import Image from 'next/image';
import { FaSeedling, FaSnowflake, FaLeaf } from 'react-icons/fa';

const services = [
  {
    title: 'Weekly & Bi-Weekly Lawn Mowing',
    description: `Professional mowing service with battery-powered mowers. We trim edges, blow clippings, and keep your property spotless.`,
    image: '/images/lawn-being-cut.png'
  },
  {
    title: 'Spring Cleanup & Aeration',
    description: `Thorough cleanup, dethatching, core aeration, and overseeding to get your lawn ready for the growing season.`,
    image: '/images/lawn-cut-shot.png'
  },
  {
    title: 'Snow Removal Service',
    description: `Professional snow removal for driveways, walkways, and sidewalks. Available now for winter service.`,
    image: '/images/snowblow-being-pushed.png',
    isSnowRemoval: true
  },
  {
    title: 'Fall Cleanup & Winter Prep',
    description: `Clear leaves and debris, apply winter fertilizer, and prepare your lawn for the cold months ahead.`,
    image: '/images/hero.jpg'
  }
];

export default function Services() {
  const [showModal, setShowModal] = useState(false);
  const [selectedService, setSelectedService] = useState<string | null>(null);
  const [formData, setFormData] = useState({ name: '', email: '', phone: '', address: '', lawnSize: '', drivewayLength: '', notes: '' });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [submitError, setSubmitError] = useState('');

  const handleQuoteClick = (service: typeof services[0]) => {
    setSelectedService(service.title);
    setShowModal(true);
  };

  const isValidEmail = (email: string) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  const isValidPhone = (phone: string) => phone.replace(/\D/g, '').length >= 10;

  const submitForm = async (type: string) => {
    setIsSubmitting(true);
    setSubmitError('');
    setSubmitSuccess(false);

    if (!formData.name.trim() || !isValidEmail(formData.email) || 
        !isValidPhone(formData.phone) || !formData.address.trim()) {
      setSubmitError('Please fill in all required fields correctly.');
      setIsSubmitting(false);
      return;
    }

    try {
      const response = await fetch('/api/send-email', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...formData,
          type: type
        })
      });

      const data = await response.json();
      if (data.success) {
        setSubmitSuccess(true);
        setFormData({ name: '', email: '', phone: '', address: '', lawnSize: '', drivewayLength: '', notes: '' });
        setTimeout(() => {
          setShowModal(false);
          setSubmitSuccess(false);
        }, 2000);
      } else {
        setSubmitError(data.message || 'Failed to send request. Please try again.');
      }
    } catch (error) {
      console.error('Error submitting form:', error);
      setSubmitError('An error occurred. Please try again or contact us directly.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="w-full min-h-full bg-gray-100 py-4 px-4">
      <div className="max-w-6xl mx-auto bg-white rounded-2xl shadow-md p-6 md:p-8">
        <h1 className="text-3xl md:text-4xl font-bold text-primary text-center mb-6">Our Services</h1>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-5">
          {services.map((service, idx) => (
            <div key={idx} className="bg-white rounded-xl shadow-md border border-gray-200 overflow-hidden hover:shadow-lg hover:-translate-y-1 transition-all flex flex-col">
              <div className="relative w-full h-40">
                <Image src={service.image} alt={service.title} fill className="object-cover" />
              </div>
              <div className="p-4 flex-1 flex flex-col">
                <h2 className="text-lg font-semibold text-primary mb-2">{service.title}</h2>
                <p className="text-sm text-gray-600 mb-4 flex-1">{service.description}</p>
                <button
                  onClick={() => handleQuoteClick(service)}
                  className="w-full bg-primary text-white py-2.5 rounded-lg font-semibold text-sm hover:bg-primary-dark transition-all min-h-[44px]"
                >
                  Get Quote
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Service Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl max-w-lg w-full p-6 max-h-[90vh] overflow-y-auto">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-xl font-semibold text-primary">Get Your {selectedService} Quote</h3>
              <button onClick={() => setShowModal(false)} className="text-gray-500 hover:text-gray-700 text-2xl">Ã—</button>
            </div>
            <p className="text-gray-600 mb-4">Enter your information to get a quote for this service.</p>
            <form onSubmit={(e) => { e.preventDefault(); submitForm(selectedService || 'Service Request'); }} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-primary mb-1">
                  Name <span className="text-red-600">*</span>
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                  className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-primary mb-1">
                  Email <span className="text-red-600">*</span>
                </label>
                <input
                  type="email"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  required
                  className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-primary mb-1">
                  Phone <span className="text-red-600">*</span>
                </label>
                <input
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  placeholder="(303) 555-1234"
                  required
                  className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-primary mb-1">
                  Address <span className="text-red-600">*</span>
                </label>
                <input
                  type="text"
                  value={formData.address}
                  onChange={(e) => setFormData({ ...formData, address: e.target.value })}
                  placeholder="123 Main St, Denver, CO 80202"
                  required
                  className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                />
              </div>
              {selectedService?.includes('Lawn') && (
                <div>
                  <label className="block text-sm font-medium text-primary mb-1">
                    Lawn Size (sq ft) <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="number"
                    value={formData.lawnSize}
                    onChange={(e) => setFormData({ ...formData, lawnSize: e.target.value })}
                    placeholder="e.g., 5000"
                    required
                    className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                  />
                </div>
              )}
              {selectedService?.includes('Snow') && (
                <div>
                  <label className="block text-sm font-medium text-primary mb-1">
                    Driveway Length (feet) <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="number"
                    value={formData.drivewayLength}
                    onChange={(e) => setFormData({ ...formData, drivewayLength: e.target.value })}
                    placeholder="e.g., 30"
                    required
                    className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20 min-h-[44px]"
                  />
                  <small className="text-gray-500 text-xs italic">We'll assume standard width and walkway</small>
                </div>
              )}
              <div>
                <label className="block text-sm font-medium text-primary mb-1">Additional Notes (optional)</label>
                <textarea
                  value={formData.notes}
                  onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                  rows={3}
                  placeholder="Any special instructions..."
                  className="w-full px-3 py-2 border border-primary-light rounded-lg focus:border-primary focus:outline-none focus:ring-2 focus:ring-primary/20"
                />
              </div>
              <button
                type="submit"
                disabled={isSubmitting}
                className="w-full bg-primary text-white py-3 rounded-lg font-semibold hover:bg-primary-dark transition-all disabled:bg-primary-light disabled:cursor-not-allowed min-h-[48px]"
              >
                {isSubmitting ? 'Submitting...' : 'Get Quote'}
              </button>
            </form>
            {submitSuccess && (
              <div className="mt-4 p-3 bg-primary-light/20 text-primary rounded-lg text-center">
                Request sent! We'll contact you soon.
              </div>
            )}
            {submitError && (
              <div className="mt-4 p-3 bg-red-100 text-red-700 rounded-lg text-center">
                {submitError}
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

